import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { LoginService } from '../login/login.service';
import { BlogService } from '../blog/blog.service';


@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})
export class AdminComponent implements OnInit {

  nav_list = [
    ['Blog List', 'fas fa-home'],
    ['Create Blog', 'fas fa-home'],
  ];
  nav_selected: string; 
  constructor(public LS: LoginService,public BS: BlogService, private cdr: ChangeDetectorRef ) { 
    LS.addChild(this);
    BS.addChild(this);
  }

  ngOnInit() {
    this.nav_selected = this.nav_list[0][0];
  }
  
  addToTeam(user){
    var disc = prompt('Enter role to display', 'Web Developer');
    user.info.iss = 'in_team';
    user.info.team_disc = disc;
    this.LS.updateUserInfo(user);
  }
  removeFromTeam(user) {
    user.info.iss = '';
    delete user.info.team_disc;
    this.LS.updateUserInfo(user);
  }

  refresh() {
    try{
    this.cdr.detectChanges();
    } catch (e) {}
  }
}
