import { Component, OnInit, ChangeDetectorRef, NgZone } from '@angular/core';
import { LoginService } from '../login/login.service';
import '../../assets/js/jquery.js';
import '../../assets/js/jquery.scrollTo-1.4.2-min.js';
import '../../assets/js/jquery.localscroll-1.2.7-min.js';
import { BlogService } from '../blog/blog.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss',
    '../../assets/css/bootstrap.css',
    '../../assets/css/style.css',
    // '../../assets/css/prettyPhoto.css',
    '../../assets/css/fontello.css',
    '../../assets/css/bootstrap-responsive.scss',
  ]
})
export class HomeComponent implements OnInit {

  constructor(public LS: LoginService, public BS: BlogService,
              private cdr: ChangeDetectorRef, private ngZone: NgZone, private router: Router) {
    LS.addChild(this);
    BS.addChild(this);
  }
  public navigate(commands: any[]): void {
    this.ngZone.run(() => this.router.navigate(commands)).then();
}

  ngOnInit() {
  }

  public scrollTo(name) {
    console.log('going to :'+ name);
    document.getElementById(name).scrollIntoView({ behavior: 'smooth', block: 'start', inline: 'nearest' });
  }
  public refresh() {
    try {
      this.cdr.detectChanges();
    } catch (e) {}
  }



}
